                    </div>
                    <div class="submitBtn_91" :class='inputMoney && inputMoney != 0 ? "GradientAllowBtn" : ""' @click="clickSubscribe()">Subscribe Now</div>
                    <div class="subscribeAlert">Early redemption will lose interest and pay a 5% service fee.</div>
                </div>
                <div class="noSubscribeBox" v-else-if="detaileData.subscription.status == 'not_started'">
                    <img class='noSubscribeIcon' src="../assets/images/noStartIcon.png" alt="" style="margin-top: 1.14rem; margin-bottom:.14rem;">
                    <p style="margin-bottom: .02rem;">Subscription will start in <span style="color:#FF8D28;">{{countdown(detaileData.subscription.countdown)}}</span></p>
                    <p>Please stay tuned and come to subscribe in time.</p>
                </div>
                <div class="noSubscribeBox" v-else-if="detaileData.subscription.status == 'completed'">
                    <img class='noSubscribeIcon' src="../assets/images/soldOutIcon.png" alt="" style="margin-top: 1.14rem; margin-bottom:.14rem;">
                    <p style="margin-bottom: .02rem;">Sold out on {{detaileData.subscription.completeTime}}</p>
                    <p>No more subscriptions for this movie</p>
                </div>
            </div>
            <div class="whiteBox" style="padding: .3rem .31rem .12rem;" v-if="detaileData.recentlyPosted">
                <div class="recentlyVideoTit">Recently posted videos</div>
                <div class="recentlyVideoBox flexStart">
                    <div class="recentlyItem">
                        <div class="recentlyImgBox">
