                        </div>
                    </div>
                    <div class="recentlyItem">
                        <div class="recentlyImgBox">
                            <div class="eyeBox flexStart">
                                <img class='eyeIcon' src="" alt="">
                                <p class="eyeNumber">999999</p>
                            </div>
                            <img class='recentlyImg' src="" alt="">
                        </div>
                        <div class="recentlyUserBox flexStart">
                            <img class='recentlyUserLogo' src="" alt="">
                            <p class="recentlyUserName">Charlotte Runolfsdo</p>
                        </div>
                    </div>
                    <div class="recentlyItem">
                        <div class="recentlyImgBox">
                            <div class="eyeBox flexStart">
                                <img class='eyeIcon' src="" alt="">
                                <p class="eyeNumber">999999</p>
                            </div>
                            <img class='recentlyImg' src="" alt="">
                        </div>
                        <div class="recentlyUserBox flexStart">
                            <img class='recentlyUserLogo' src="" alt="">
                            <p class="recentlyUserName">Charlotte Runolfsdo</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <van-popup v-model="showUpgreadAlert" class='upgreadAlertBox flexCenter' style="" v-if="detaileData && detaileData.subscription">
            <img class='modelCloseIcon' src="../assets/images/modelCloseIcon.png" alt="" @click="showUpgreadAlert = false">
            <img class='upgreadIcon' src="../assets/images/upgreadIcon.png" alt="">
            <p class="upgreadText">This copyright requires<br>a higher VIP level to access</p>
            <div class="requiredText flexStart" style="margin-bottom: .06rem;">
                <span class='blueText'>Required level: </span>
                <img class='smallVipIcon' src="../assets/images/smallV0Icon.png" alt="" v-if="detaileData.subscription.requireLevel == 0">
                <img class='smallVipIcon' src="../assets/images/smallV1Icon.png" alt="" v-else-if="detaileData.subscription.requireLevel == 1">
                <img class='smallVipIcon' src="../assets/images/smallV2Icon.png" alt="" v-else-if="detaileData.subscription.requireLevel == 2">
                <img class='smallVipIcon' src="../assets/images/smallV3Icon.png" alt="" v-else-if="detaileData.subscription.requireLevel == 3">
                <img class='smallVipIcon' src="../assets/images/smallV4Icon.png" alt="" v-else-if="detaileData.subscription.requireLevel == 4">
                <img class='smallVipIcon' src="../assets/images/smallV5Icon.png" alt="" v-else-if="detaileData.subscription.requireLevel == 5">
                <span class='blueText'> {{'v'+detaileData.subscription.requireLevel}}</span>
            </div>
            <div class="currentText flexStart">
                <span class='redText'>Current level: </span>
                <img class='smallVipIcon' src="../assets/images/smallV0Icon.png" alt="" v-if="detaileData.subscription.myLevel == 0">
                <img class='smallVipIcon' src="../assets/images/smallV1Icon.png" alt="" v-else-if="detaileData.subscription.myLevel == 1">
                <img class='smallVipIcon' src="../assets/images/smallV2Icon.png" alt="" v-else-if="detaileData.subscription.myLevel == 2">
                <img class='smallVipIcon' src="../assets/images/smallV3Icon.png" alt="" v-else-if="detaileData.subscription.myLevel == 3">
                <img class='smallVipIcon' src="../assets/images/smallV4Icon.png" alt="" v-else-if="detaileData.subscription.myLevel == 4">
                <img class='smallVipIcon' src="../assets/images/smallV5Icon.png" alt="" v-else-if="detaileData.subscription.myLevel == 5">
                <span class='redText'> {{'v'+detaileData.subscription.myLevel}}</span>
            </div>
            <div class="submitBtn_91 GradientAllowBtn" style="margin-top: .56rem;width: 100%;" @click="goPage('/VIP')">Upgrade Now</div>
        </van-popup>
        <van-dialog class='alertModelBox' v-model="alertModelBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Failed
                    <img class='alertModelCloseIcon' @click="alertModelBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">Subscription amount is lower than minimum requirements.<br>The starting amount is {{moneySymbol+detaileData.subscription.min}} and no upper limit.</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <div class="submitBtn_91 blueAllowBtn" style="width: 100%;" @click="alertModelBox = false">OK</div>
                    </div>
                </div>
            </template>
        </van-dialog>
        <van-dialog class='alertModelBox' v-model="pamentBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Note
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">Please return after payment is completed.</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <a class="submitBtn_91 blueAllowBtn" target="_blank" :href="payUrl" style="width: 100%;"  @click="pamentBox = false">Continue</a>
                    </div>
                </div>
            </template>
        </van-dialog>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'MovieDetails',
		data: function(){
			return {
                showLoading: true,
				bgWrapHeight: 200,
                openStroe: false,
                moneySymbol: https.moneySymbol,
                detailId: this.$route.params.id,
                detaileData: null,
                subscriptionCount: '',
                inputMoney: '',
                showUpgreadAlert: false,
                alertModelBox: false,
                payUrl: '',
                pamentBox: false,
                timer2: null,
			}
		},
		created(){
			this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    id: this.detailId
                }
				https.fetchGet('/api/library/item',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.detaileData = res.data
                        this.subscriptionCount = res.data.subscriptionCount
                        this.inputMoney = this.detaileData.subscription.min
                        if(this.detaileData.subscription.status == 'not_started'){
                            let countdown = this.detaileData.subscription.countdown
                            if(countdown > 0) {
                                countdown --
                                this.detaileData.subscription.countdown = countdown
                            }else {
                                this.getData()
                            }
                        }
                        if(this.detaileData.subscription.status == 'not_open') {
                            this.openStroe = true
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
                    console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            selectClassType(item){
                this.selectClassTypeId = item.id
            },
            countdown: function(countdown){
                if(countdown/60/60/24 > 1) {
                    if(countdown/60/60/24 > 2) {
                        return parseInt(countdown/60/60/24) + ' days'
                    }else {
                        return parseInt(countdown/60/60/24) + ' day'
                    }
                }else {
                    var sec = parseInt(countdown);
                    var min= 0;
                    var hour= 0;
                    if(sec > 60) {
                        min= parseInt(sec/60);
                        sec = parseInt(sec%60);
                        if(min> 60) {
                            hour= parseInt(min/60);
                            min= parseInt(min%60);
                        }
                    }
                    var result = `${hour.toString().padStart(2,'0')}:${min.toString().padStart(2,'0')}:${sec.toString().padStart(2,'0')}`
                    return result;
                }
            },
            clickSubscribe(){
                if(this.inputMoney/1 < this.detaileData.subscription.min/1) {
                    this.alertModelBox = true
                }else if(this.detaileData.subscription.myLevel < this.detaileData.subscription.requireLevel) {
                    this.showUpgreadAlert = true
                }else {
                    this.goSubscribe()
                }
            },
            goSubscribe(){
                // if(!this.balanceEnough) {
                //     return
                // }
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'subscription',
                    product: this.detailId,
                    money: this.inputMoney,
                }
				https.fetchPost('/api/pay/cashier/create',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        if(res.data.subscription && res.data.subscription.id) {//balance pay success
                            this.$toast({
                                message: 'Successfully subscribed',
                                forbidClick: true,
                                icon: require('../assets/images/successIcon.png'),
                                duration: 2000,
                                className: 'resultToast'
                            });
                            setTimeout(() => {
                                this.goPage('/SubscriptionDetail/'+res.data.subscription.id)
                            }, 2000);
                        }else if(res.data.openUrl) {
                            this.payUrl = res.data.openUrl
                            this.pamentBox = true
                            let that = this
                            this.timer2 = setInterval(() => {
                                that.checkPayResult(res.data.orderId)
                            }, 10*1000);
                        }
                        
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
                    console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
                        
					}
				})
            },
            checkPayResult: function(orderId){
                let params = {
                    orderId: orderId
                }
				https.fetchGet('/api/pay/cashier/query',params).then((data) => {
					let res = data.data
					if(res.code == 0) {
						if(res.data.status == 1) {
                            if(this.timer2) {
                                clearInterval(this.timer2)
                            }
                            this.goPage('/SubscriptionDetail/'+orderId)
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
                    console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.checkPayResult()
					}
				})
			},
            clickOpenStore(){
                this.openStroe = !this.openStroe
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
        computed: {
            computed_interest: function(){
                if(this.inputMoney && this.inputMoney > 0) {
                    console.log(this.detaileData.subscription.interestRate)
                    let computed_interest = ((this.detaileData.subscription.interestRate/1 * 100)/(this.detaileData.subscription.cycle/1)).toFixed(2)
                    return computed_interest + '%'
                }else {
                    return '--'
                }
            },
            computed_profit: function(){
                if(this.inputMoney && this.inputMoney > 0) {
                    let computed_profit = (this.inputMoney * (this.detaileData.subscription.interestRate/1)).toFixed(2)
                    return this.moneySymbol + computed_profit
                }else {
                    return '--'
                }
            },
            computed_amount: function(){
                if(this.inputMoney && this.inputMoney > 0) {
                    let computed_amount = (this.inputMoney/1 + (this.inputMoney/1 * (this.detaileData.subscription.interestRate/1))).toFixed(2)
                    return this.moneySymbol +computed_amount
                }else {
                    return '--'
                }
            },
            computed_date: function(){
                if(this.inputMoney && this.inputMoney > 0) {
                    let sysDate = new Date()
                    let nowDate = sysDate.toLocaleDateString()
                    let d = new Date(nowDate);  
                    d.setDate(d.getDate()+1+this.detaileData.subscription.cycle)

                    console.log(nowDate)
                    console.log(d.toLocaleDateString())
                    // let computed_date = 
                    return d.toLocaleDateString()
                }else {
                    return '--'
                }
            },
            balanceEnough: function(){
                if(this.inputMoney && this.inputMoney > 0) {
                    if(this.detaileData.balance) {
                        if(this.inputMoney/1 > this.detaileData.balance/1) {
                            return false
                        }else {
                            return true
                        }
                    }else {
                        return true
                    }
                }else {
                    return true
                }
            },
        },
        destroyed() {
            if(this.timer2) {
                clearInterval(this.timer2)
            }
        }
	}
</script>
